
import {Switch,Route} from 'react-router-dom'
import Signin from './signin'
import Form from './Form'
import User from './user'
import './users.css'

function Users(){

  

  

  return(
    <Switch>
        <Route path="/" exact>
             <Form />
        </Route>
   
          <Route path="/users">
            <User />
          </Route>
          
          <Route path="/signin">
             <Signin />
          </Route> 
      
    </Switch>
    
  )
}
export default Users