import React, {Component} from 'react'
import './form.css'

class Form extends Component{

  state = {
    user: {
      name: '',
      phone: '',
      adress: '',
      id_num: ''
    }
  }
    
  

  login=e=>{
      e.preventDefault()
      console.log(JSON.stringify(this.state.user))
      fetch('https:127.0.0.1:8000/api/users/',{
        method: "POST",
        headers: {"Content-Type": "application/json",
                    'X-CSRFToken': 'csrftoken'
        },
                 
        body: JSON.stringify(this.state.user)
      })
      .then(res=>res.json())
      .then(res=>console.log(res))
      .catch(e=>console.log(e))
  
  }
  changeHandler=e=>{
    const us = this.state.user
    us[e.target.name] = e.target.value
    this.setState({user : us})
  }
    
  
  
  render(){
    return(
      <div className="form-wrap">
        <form className="form">
          <input type="text" name="name" value={this.state.name} onChange={this.changeHandler} placeholder="ФИО"/>
          <input type="number" name="phone" value={this.state.phone} onChange={this.changeHandler} placeholder="Номер телефона"/>
          <input type="text" name="adress" value={this.state.adress} onChange={this.changeHandler} placeholder="Адрес"/>
          <input type="number" name="id_num" value={this.state.id_num} onChange={this.changeHandler} placeholder="ИНН"/>
          <button  onClick={this.login}>Добавить</button>
        </form>
      </div>
    )
  }

}

export default Form